.sidebarWrap {
   width: 225px;
   height: 100vh;
   background-color: var(--bgSidebar);
   position: relative;
   top: 0; 
   @include minMd() {
      width:260px;
      }     
}

.sidebar {
   width: 100%;
   height: 100%;
   padding: 14px 14px 24px 14px;
   @include minMd() {
      padding: 24px;      
}
}

.sbHeader {
   display: flex;
   align-items: center;
   margin-bottom: 70px;
   @include minMd() {
      margin-bottom: 60px;         
}
}

.sbLogo {
   fill: #1F1F1F;
   margin-right: 8px;
}

.sbTitle {
   display: flex;
   color: #161616;
   text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
   font-family:map-get($fonts, b), sans-serif;
   font-size: 16px;
   font-style: normal;
   // font-weight: 600;
   line-height: normal;
   letter-spacing: -0.64px;
@include minMd() {
   text-shadow: none; }
}

.sbSubtitle {
   color: rgba(22, 22, 22, 0.50);
   font-family: map-get($fonts, r), sans-serif;   
   font-size: 12px;
   font-style: normal;
   line-height: normal;
   letter-spacing: -0.24px;
      margin-bottom: 8px;
   }

.sbBoards {
   display: flex;
   align-items: center;
   // justify-content: center;
}

.sbCreateBoardBlock {
   display: flex;
   align-items: center;
   width: 197px;
   height: 70px;
   border-top: 1px solid rgba(22, 22, 22, 0.10);
   border-bottom: 1px solid rgba(22, 22, 22, 0.10);
   margin-bottom: 40px;
   @include minMd() {
   width: 212px;      }
}

.sbCreateBoardText {
   width: 76px;
   height: 42px;
   display: flex;
   color: #161616;
   font-family: map-get($fonts, m), sans-serif;   
   font-size: 14px;
   font-style: normal;
   // font-weight: 500;
   line-height: normal;
   letter-spacing: -0.28px;
   margin-right: 77px;
      @include minMd() {
   margin-right: 96px;         }
}

.sbCreateBoardButton {
   display: block;
   width: 40px;
   height: 36px;
   border: none;
   border-radius: 6px;
   background: #BEDBB0;
   // flex-shrink: 0;
}

.sbCreateBoardLogo {
   flex-shrink: 0;
   stroke: #161616;
}

.sbHelp {
   width: 197px;
   height: 238px;
   display: flex;
   flex-direction: column;
   flex-shrink: 0;
   border-radius: 8px;
   background: #F6F6F7;
   margin-top: 51px;
   margin-bottom: 24px;
   @include minMd() {
      width: 212px;
      height: 272px;
      margin-top: 99px;
            }
   @include minLg() {
      margin-top: 40px;
   }
}

.sbHelpText {
   width: 168px;
   height: 80px;
   flex-shrink: 0;
   color: #161616;
   font-family: map-get($fonts, r), sans-serif;
   font-size: 12px;
   font-style: normal;
   line-height: 16px;
   letter-spacing: -0.24px;
   margin: 0 14px 18px 15px;

   .cbTaskProSpan {
      color: #BEDBB0;
   }
   @include minMd() {
      width: 172px;
      height: 102px;
      margin: 0 20px 18px 20px;
      font-size: 14px;
      font-style: normal;
      line-height: 20px;
      letter-spacing: -0.28px;
   }
}

.sbHelpButton {
   padding: 0;
   margin: 0 89px 14px 14px;
   border: none;
   width: 92px;
   height: 20px;
   display: inline-flex;
   background-color: transparent;
   justify-content: flex-start;
   align-items: center;
   @include minMd() {
      margin: 0 98px 20px 20px;
         }
}

.sbNeedhelpSvg {
   stroke: #161616;
   fill: #fff;
   margin-right: 7px;
}

.sbNeedHelpButtonText {
   color: #161616;
   font-family: map-get($fonts, m), sans-serif;   
   font-size: 12px;
   font-style: normal;
   line-height: normal;
   letter-spacing: -0.24px;
}

.sbLogoutButton {
   padding: 0;
   border: none;
   width: 98px;
   height: 32px;
   display: inline-flex;
   background-color: transparent;
   justify-content: flex-start;
   align-items: center;
      @include minMd() {
   width: 105px;         }
  
}

.sbLogoutSvg {
   display: flex;
   justify-content: center;
   align-items: center;
   stroke: #BEDBB0;
   fill: #fff;
   margin-right: 14px;
}

.sbHelp::before {
   content: "";
   overflow: hidden;
   display: block;
}
.sbHelpPngCactus {
   width: 54px;
   height: 78px;
   margin: 14px 129px 14px 14px;   
   flex-shrink: 0;
   background: url(../../images/help-cactus.png), no-repeat;
      @include minMd() {
      margin: 20px 138px 14px 20px;
}
}

.sbLogoutText {
color: #161616;
   font-family: map-get($fonts, m), sans-serif;
   font-size: 14px;
   font-style: normal;
   line-height: normal;
}