.headerWrap {
  padding: 14px 20px;
  background-color: var(--bgHeader);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  @include minMd() {
    padding: 18px 32px;
  }
}

.headerTextWrap {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}

.headerUserInfoWrap {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.headerUserName {
  color: var(--bgHeader);
  font-family: map-get($fonts, m);
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: -0.28px;
  margin-right: 8px;
}

.headerAvatar {
  width: 32px;
  height: 32px;
  background-image: url(../../images/user-default-light.png);
  background-size: cover;
  border-radius: 8px;
}

.svgTheme {
  stroke: rgba(22, 22, 22, 0.8);
  stroke-width: 1.5px;
  fill: none;
  margin-left: 4px;
  margin-right: 14px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.themeWrap {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.svgSideBarOpen {
  stroke: var(--primary);

  @include minMd() {
    width: 34px;
    height: 34px;
  }
  @include minLg() {
    display: none;
  }
}

.btnSideBarOpen {
  border: none;
  background: transparent;
}

.themeText {
  color: rgba(22, 22, 22, 0.8);
  font-family: map-get($fonts, m);
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: -0.28px;
}

.AFNavResWr {
  @include minLg() {
    display: grid;
    grid-template-columns: 1fr 4fr;
    grid-template-areas:
      'sidebar header'
      'sidebar content';
  }
  &Head {
    @include minLg() {
      grid-area: header;
      height: 10vh;
    }
  }
  &Aside {
    @include minLg() {
      grid-area: sidebar;
      height: 100vh;
    }
  }
  &Main {
    @include minLg() {
      grid-area: content;
      width: auto;
      height: 90vh;
    }
  }
}


.btnThemeOpen {
  border: none;
  background: transparent;
  padding: 0;
}

.dropdownThemeWrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  position: relative;
  
}

.themeHeaderWrap {
  display: flex;
  flex-direction: row;
  
}

.dropdownThemeMenu {
  background-color: var(--bgHeader);
  width: 100px;
  height: 107px;
  text-align: center;
  border-radius: 8px;
  border: 1px solid var(--borderTheme);
  box-shadow: 0px 4px 16px 0px rgba(17, 17, 17, 0.10);
  padding: 18px 44px 18px 18px;
  display: flex;
  justify-content: left;
  align-items: flex-start;
  position: absolute;
  top: 100%;
  left: -10%;
}


.dropdownThemeList {
  cursor: pointer;
}

.dropdownThemeList:not(:last-child) {
  padding-bottom: 4px;
}

.themeMenuItem {
  color: var(--textThemeMenu);
    font-family: map-get($fonts, r);
    font-size: 14px;
    font-style: normal;
    line-height: normal;
    letter-spacing: -0.28px;
}

.themeMenuItem:hover,
.themeMenuItem:focus {
  color: var(--accent);
}